html {
    font-size: 625%; /* 100px ÷ 16px × 100% = 62.5% */
}

/* 以大屏为主来显示，媒体查询顺序要从大到小，
    否则媒体查询顺序要从小到大 */
@media screen and (max-width: 1012px) {

    h1 {
        margin-left: 0.625rem;
        margin-right: 0.625rem;
        padding-top: 0.42rem;
        padding-bottom: 0.42rem;
        font-size: 0.56rem;
    }

    h4 {
        font-size: 0.18rem;
    }

    header {
        padding-left: 0.6rem;
        padding-right: 0.6rem;
    }

    .dot {
        width: 0.14rem;
        height: 0.14rem;
    }

    .img-header-logo {
        width: 14.5%;
        min-width: 1.03rem;
    }

    .content-container {
        margin: 0 0.6rem;
    }

    .content-container img {
        width: 0.22rem;
    }

    .content-container .main-content-text {
        font-size: 0.21rem;
        line-height: 0.3rem;
        padding: 0 0.25rem;
    }

    .main-devices-container {
        margin-top: 0.42rem;
    }

    .main-devices-container p {
        font-size: 0.42rem;
        font-weight: var(--font-weight-light);
        padding: 0.41rem 0;
    }

    .main-devices-list {
        align-items: flex-start;
        padding: 0 2%;
    }

    .single-device-section:nth-child(even) {
        margin: 0.35rem 0;
    }

    .single-device-section button {
        border: solid 1.5px white;
        border-radius: 3px;
        font-size: 0.22rem;
        width: calc(100vw/6);
        max-width: 150px;
        height: auto;
        margin: 0.22rem 0;
    }

    #main-footer {
        margin-top: 0.5rem;
        word-wrap: break-word;
        position: relative;
    }

    .f-container {
        padding: 0.52rem 5%;
    }

    .img-footer-logo {
        width: 40%;
        margin-bottom: 0.36rem;
    }

    .mb-5 {
        margin-bottom: 0.3rem;
    }

    .f-container h4 {
        padding-top: 0.1rem;
        padding-bottom: 0.1rem;
    }
      
      .f-container p {
        padding-top: 0.1rem;
        padding-bottom: 0.1rem;
    }
      
      .f-container li {
        padding-top: 0.1rem;
        padding-bottom: 0.1rem;
    }

    .p-tb-16 {
        padding-top: 0.1rem;
        padding-bottom: 0.1rem;
    }

    .footer-secondary {
        font-size: 0.18rem;
    }

    .footer-link-button {
        border-radius: 5px;
        font-size: 0.18rem;
        padding: 0.08rem 0.12rem;
    }

    /* Device common */
    .top-container {
        margin-top: 0.15rem;
        padding-left: 0.6rem;
        padding-right: 0.6rem;
        padding-bottom: 0.2rem;
    }

    .top-container-title {
        display: block;
        flex-direction: row;
        margin-bottom: 0.4rem;
    }

    .top-container-title h2 {
        margin-right: 0.18rem;
        font-size: clamp(0.36rem, calc(100vw/21.3), 0.49rem);
    }

    .top-container-img {
        margin: 0 auto;
        margin-top: 0.2rem;
        width: 32.8%;
        max-width: 342px;
    }

    .top-bottom-bar {
        height: 0px;
        display: none;
    }

    .bottom-container {
        padding: 0.3rem;
        margin-top: 0px;
    }

    .bottom-container img {
        width: 0.31rem;
    }

    .bottom-container p {
        font-size: 0.23rem;
        margin-left: 8px;
        line-height: 0.35rem;
    }

    .device-content {
        padding-left: 6.5%;
        padding-right: 6.5%;
    }

    .device-content li {
        padding: 0.12rem 0;
    }

    .device-second-title {
        font-size: clamp(0.22rem, calc(100vw/34.9), 0.28rem);
        font-weight: var(--font-weight-bold);
        line-height: 0.4rem;
        margin-top: 0px;
        margin-bottom: 0.1rem;
    }

    .device-content-p {
        font-size: 0.23rem;
        line-height: 0.36rem;
    }

    .number-box {
        width: 0.32rem;
        height: 0.32rem;
        margin-right: 0.14rem;
        padding: 0px;
    }

    .number-box-p {
        font-size: 0.28rem;
    }

    .device-content-img {
        width: 70%;
        margin: 0.2rem auto 0.5rem;
        display: block;
        /* width: 5.48rem; */
        min-width: 4rem;
        max-width: 5.48rem;
    }

    .device-content-img img {
        width: 100%;
        height: auto;
        margin-top: 0.2rem;
    }

    .first-part-img {
        display: block;
        width: clamp(4.62rem, calc(100vw/1.66), 6rem); /*463 */
        height: auto;
    }

    .first-part-img img {
        width: 100%;
        height: auto;
    }

    .second-part-img img:first-child {
        display: none;
    }

    .iwatch-part-img-d-none {
        display: none;
    }

    .android-part-img {
        display: block;
    }

    .fifth-part-img img:first-child {
        display: none;
    }

    .sixth-part-img img:first-child {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    h1 {
        margin-left: 0.25rem;
        margin-right: 0.25rem;
        padding-top: 0.2rem;
        padding-bottom: 0.2rem;
        font-size: 0.24rem;
    }

    h4 {
        font-size: 0.11rem;
    }

    .f-wrap {
        flex-wrap: wrap;
    }

    .dot {
        width: 0.1rem;
        height: 0.1rem;
    }

    header {
        padding-left: 5%;
        padding-left: 5%;
    }

    .img-header-logo {
        width: 20%;
        max-width: 1.03rem;
        margin-bottom: 0;
    }

    .content-container {
        margin: 0 0.21rem;
    }

    .content-container img {
        width: 0.12rem;
    }

    .content-container .main-content-text {
        font-size: 0.14rem;
        line-height: 0.18rem;
        padding: 0 0.08rem;
    }

    .main-devices-container {
        margin-top: 0.24rem;
    }

    .main-devices-container p {
        font-size: 0.2rem;
        font-weight: 300;
        padding: 0.22rem 0;
    }

    .main-devices-list {
        align-items: center;
        justify-content: center;
        padding: 0 4%;
    }

    .single-device-section {
        width: 33%;
    }

    .single-device-section:nth-child(even) {
        margin: 0;
    }

    .single-device-section button {
        border: solid 0.5px white;
        border-radius: 2px;
        font-size: 0.12rem;
        width: calc(100vw/5);
        max-width: 120px;
        height: auto;
        margin: 0.15rem 0;
    }

    #main-footer {
        margin-top: 0;
    }

    .f-container {
        padding: 0.32rem 5.3%;
    }

    .img-footer-logo {
        width: 33%;
        margin-bottom: 0.06rem;
    }
    
    .col-lg-4 {
        min-width: 49%;
    }

    .mb-5 {
        margin-bottom: 0.18rem;
    }

    .f-container h4 {
        padding-top: 0.04rem;
        padding-bottom: 0.04rem;
    }
      
      .f-container p {
        padding-top: 0.04rem;
        padding-bottom: 0.04rem;
    }
      
      .f-container li {
        padding-top: 0.04rem;
        padding-bottom: 0.04rem;
    }

    .p-tb-16 {
        padding-top: 0.04rem;
        padding-bottom: 0.04rem;
    }

    .footer-secondary {
        font-size: 0.11rem;
    }

    .footer-link-button {
        border-radius: 3px;
        font-size: 0.11rem;
        padding: 0.04rem 0.08rem;
    }

    /* Device common */
    .top-container {
        margin-top: 0.2rem;
        padding-left: 8.5%;
        padding-right: 8.5%;
        padding-bottom: 0.2rem;
    }

    .top-container-title {
        display: block;
        flex-direction: row;
        margin-bottom: 0.4rem;
    }

    .top-container-title h2 {
        margin-right: 0px;
        font-size: clamp(0.22rem, calc(100vw/17), 0.36rem);
    }

    .top-container-img {
        margin: 0 auto;
        margin-top: 0.2rem;
        width: 50%;
        min-width: 162px;
        max-width: 250px;
    }

    .bottom-container {
        padding: 0.2rem;
    }

    .bottom-container img {
        width: 0.18rem;
    }

    .bottom-container p {
        font-size: 0.14rem;
        margin-left: 4px;
        margin-top: -3px;
        line-height: 0.25rem;
    }

    .device-content {
        padding-left: 8.5%;
        padding-right: 8.5%;
    }

    .device-content li {
        padding: 4px 0;
    }

    .device-second-title {
        font-size: 0.16rem;
        font-weight: clamp(0.16rem, calc(100vw/23.4), 0.22rem);
        line-height: 0.26rem;
    }

    .device-content-p {
        font-size: 0.14rem;
        line-height: 0.25rem;
    }

    .number-box {
        width: 22px;
        height: 22px;
        margin-right: 0.1rem;
    }

    .number-box-p {
        font-size: 0.2rem;
    }

    .info-img {
        width: 0.1rem;
        margin: 0 2px;
    }

    .device-content-img {
        margin: 0 auto 0.24rem;
        width: 75%;
        min-width: 2.67rem;
        max-width: 4rem;
    }
}

