:root {
  --theme-main-color: #00a3e0;
  --theme-minor-color: #ABCD03;
  --theme-gray-color: #424142;
  --theme-ligher-gray-color: #666666;
  --theme-bgGray-color: #f1f1f1;

  --font-weight-thin: 100;
  --font-weight-extra-light: 200;
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semi-bold: 600;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;
  --font-weight-heavy: 900;

  --max-width-body: 1920px;
  --min-width-body: 750px;
}

::after, ::before {
  box-sizing: border-box;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, strong, p, a, img, ol, ul, li, form, label {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  vertical-align: baseline;
  background: transparent;
}

.bgcolor {
  background-color: cyan;
  flex: 1;
}

body, select {
  font-family: 'Poppins',Helvetica,Arial,Lucida,sans-serif;
}

header, footer, nav, section {
  display: block;
}

header {
  position: relative;
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
}

footer {
  background-color: var(--theme-bgGray-color);
}

body {
  font-size: 14px;
}

strong {
  font-weight: 700;
}

a {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

.container-xl {
  max-width: 1920px;
  margin-right: auto;
  margin-left: auto;
}

h1 {
  display: block;
  margin-left: 1rem;
  margin-right: 1rem;
  padding-top: 0.64rem;
  padding-bottom: 0.64rem;
  text-align: center;
  color: var(--theme-gray-color);
  font-weight: var(--font-weight-bold);
  font-size: 5.7em; /* 5.7 * 14px */
}

h2 {
  display: block;
  color: var(--theme-gray-color);
  font-weight: var(--font-weight-bold);
}

h4 {
  display: block;
  font-size: 0.24rem;
  color: var(--theme-main-color);
  font-weight: var(--font-weight-semi-bold);
}

.d-flex {
  display: flex;
}

.d-none {
  display: none;
}

.f-wrap {
  flex-wrap: nowrap;
}

.align-center {
  align-items: center;
}

.fw-bold {
  font-weight: var(--font-weight-bold);
}

.dot {
  position: absolute;
  width: 0.18rem;
  height: 0.18rem;
  border-radius: 50%;
  top: 0;
  right: 0;
  background-color: var(--theme-minor-color);
}

.img-header-logo {
  display: inline-block;
  width: calc(100vw/6.86);
  margin-top: 0.6rem;
  margin-bottom: 0.2rem;
}

.theme-main-color {
  color: var(--theme-main-color);
}

.content-container {
  display: flex;
  margin: 0 1rem;
}

.content-container img {
  display: inline-block;
  width: 0.32rem;
  height: 100%;
  margin: auto;
}

.content-container .main-content-text {
  color: var(--theme-gray-color);
  font-size: 0.28rem;
  line-height: 0.42rem;
  padding: 0 0.42rem;
}

.theme-main-bgcolor {
  background-color: var(--theme-main-color);
}

.main-devices-container {
  margin-top: 0.6rem;
  text-align: center;
}

.main-devices-container p {
  font-size: 0.64rem;
  font-weight: var(--font-weight-light);
  color: white;
  padding: 0.6rem 0;
}

.main-devices-list {
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 4%;
}

.single-device-section {
  text-align: center;
  width: 20%;
}

.single-device-section:nth-child(even) {
  margin: 0.7rem 0;
}

.single-device-section img {
  width: 100%;
}

.single-device-section button {
  background-color: transparent;
  border: solid 3px white;
  border-radius: 4px;
  color: white;
  font-size: 0.36rem;
  font-weight: var(--font-weight-medium);
  width: calc(100vw/4.64);
  max-width: 220px;
  height: auto;
  padding: 2.5% 0;
  margin: 0.3rem 0;
}

#main-footer {
  margin-top: 1rem;
  word-wrap: break-word;
  position: relative;
}

.f-container {
  max-width: 1820px;
  padding: 0.73rem 0.53rem 0.73rem;
}

#footer-widgets {
  flex-wrap: wrap;
  overflow: hidden;
  width: 100%;
}

.footer-widget ::before {
  display: none;
}

.img-footer-logo {
  display: inline-block;
  width: 60%;
  margin-bottom: 0.66rem;
}

.col-lg-4 {
  width: 24%;
}

.mb-5 {
  margin-bottom: 0.43rem;
}

.f-container h4 {
  padding-top: 0.16rem;
  padding-bottom: 0.16rem;
}

.f-container p {
  padding-top: 0.16rem;
  padding-bottom: 0.16rem;
}

.f-container li {
  padding-top: 0.16rem;
  padding-bottom: 0.16rem;
}

.p-tb-16 {
  padding-top: 0.16rem;
  padding-bottom: 0.16rem;
}

.footer-secondary {
  font-size: 0.24rem;
  color: var(--theme-ligher-gray-color);
}

.footer-link-button {
  border-radius: 6px;
  border-style: none;
  color: white;
  font-size: 0.24rem;
  font-weight: 500;
  padding: 0.12rem 0.15rem;
}

/* Device common */
.top-container {
  margin-top: 0.1rem;
  padding-left: 5%;
  padding-right: 8%;
  padding-bottom: 0.2rem;
}

.top-container-title {
  display: flex;
  align-items: center;
  margin-bottom: 0.4rem;
}

.top-container-title h2 {
  font-size: clamp(0.49rem, calc(100vw/25), 0.76rem);
  flex: 4;
}

.top-container-img {
  flex: 1;
  max-width: 324px;
}

.top-container-img img {
  width: 100%;
  height: auto;
}

.top-bottom-bar {
  width: 6.53%;
  height: 8px;
  background-color: var(--theme-minor-color);
}

.top-container li {
  flex: auto;
}

.bottom-container {
  display: flex;
  padding: 0.4rem 1.3rem;
  margin-top: 0.46rem;
}

.bottom-container img {
  width: 0.44rem;
  height: 100%;
}

.bottom-container p {
  color: white;
  font-size: 0.32rem;
  font-style: italic;
  margin-left: 0.12rem;
  line-height: 0.45rem;
}

.device-content {
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 0.2rem;
}

.device-content li {
  padding: 0.19rem 0;
}

.device-second-title {
  font-size: clamp(0.28rem, calc(100vw/36.4), 0.4rem);
  font-weight: var(--font-weight-bold);
  line-height: 0.6rem;
  color: var(--theme-gray-color);
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}

.device-second-title span {
  background: linear-gradient(to bottom,
     white 50%, rgba(0, 163, 224, 0.16) 51% 100%);
}

.device-content-p {
  font-size: 0.32rem;
  display: inline-block;
  vertical-align: middle;
  color: var(--theme-gray-color);
  line-height: 0.48rem;
}

.device-content-img {
  width: 75%;
  max-width: 12.7rem;
  margin: 0.43rem auto 0.74rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.device-content-img img {
  width: 45%;
  height: auto;
}

.number-box {
  position: relative;
  display: inline-block;
  width: 0.42rem;
  height: 0.5rem;
  margin-right: 0.18rem;
  padding: 0.16rem 0;
  vertical-align: middle;
}

.number-box-p {
  position: absolute;
  font-size: 0.36rem;
  font-weight: var(--font-weight-bold);
  color: var(--theme-gray-color);
  font-family: "Roboto", sans-serif;
  top: -2px;
  right: 2px;
}

.info-img {
  width: 0.24rem;
  margin: 0 4px;
}

.first-part-img {
  display: none;
}

.android-part-img {
  display: none;
}

.fifth-part-img {
  display: none;
}
